.offers{
    width: 65%;
    height: 70vh;
    display: flex;
    margin-top: 0px;
    padding: 0px 140px;
    margin-bottom: 100px;
    margin-left: 150px;
    margin-right: 150px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
}

.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;
}

.offers-left p {
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}

.offers-left button{
    width: 282px;
    padding: 15px 20px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right{
flex: 1;
display: flex;
align-items: center;
justify-content: center;
padding-top: 0; 
img{
    height: 445px;
}
}
